@import "mixins";
@import "vars";
@import "components/fonts";
@import "pages/a-index";
@import "pages/b-news";
@import "pages/c-news";
@import "pages/d-news-item";
@import "pages/e-landing";

.cookie-modal{
  position: fixed;
  z-index: 21;
  left: 16px;
  bottom: 16px;
  background-color: #fff;
  box-shadow: 0 0 8px 4px rgba(0,0,0, .15);
  width: 364px;
  max-width: calc(100vw - 32px);
  padding: 16px;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__nav{
    margin-top: 10px;
    display: flex;
  }
  &__text{
    font-size: 12px;
    line-height: 130%;
    a{
      color: $primary;
    }
  }
  &__btn{
    padding: 10px 30px;
    font-weight: 400;
    font-size: 14px;
  }
}

.detailed-news__text blockquote{
  font-size: 16px;
  line-height: calc(26 / 16);
  color: var(--gray-600);
  font-weight: 400;
  margin-bottom: 26px;
  margin-left: 40px;
  padding-left: 20px;
  border-left: 5px solid $primary;
  font-style: italic;
}

.detailed-news__text blockquote:last-child{
  margin-bottom: 0;
}


@media screen and (max-width: 768px){
  .detailed-news__text blockquote{
    font-size: 14px;
    line-height: calc(22 / 14);
    margin-bottom: 22px;
  }
}


