.wrapper-landing{
  padding-top: 0;
}

.header-landing{
  padding: 40px 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    width: 150px;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-family: $font1;
    font-size: 24px;
    line-height: 29px;
    font-weight: 600;
    text-transform: uppercase;
  }
}

.landing-group{
  padding-top: 100px;
  &:first-child{
    padding-top: 33px;
  }
  &:last-child{
    padding-bottom: 100px;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    &-col{
      max-width: 450px;
    }
  }
  &__title{
    font-size: 34px;
    line-height: 40px;
    font-weight: 600;
    font-family: $font1;
  }
  &__desc{
    margin-top: 9px;
    font-size: 26px;
    line-height: 35px;
    color: #666666;
  }
  &__desc2{
    font-size: 18px;
    line-height: 22px;
    font-weight: 600;
    font-family: $font1;
  }
  &__square{
    font-size: 32px;
    line-height: 39px;
    font-weight: 600;
    font-family: $font1;
  }
  &__bottom{
    margin-top: 35px;
  }
  &__grid{
    margin-bottom: 16px;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 80px / 6));
    gap: 16px;
    grid-auto-flow: dense;
    &:last-child{
      margin-bottom: 0;
    }
  }
  &__item{
    @include imgCover(73.5%);
    border-radius: 10px;
    grid-column: span 2;
    @media screen and (min-width: 769px){
      &._big-col{
        grid-column: span 3;
      }
      &._big-row{
        grid-row: span 2;
        padding-bottom: 150%;
        min-height: 100%;
      }
      &._big-col2{
        grid-column: span 4;
      }
      &._big-row2{
        grid-row: span 2;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header-landing{
    padding: 17px 0;
    box-shadow: 0 3px 6px rgba(#000,.05);
    &__logo{
      width: 105px;
    }
    &__title{
      display: none;
    }
  }

  .landing-group{
    padding-top: 50px;
    &:last-child{
      padding-bottom: 50px;
    }
    &__top{
      &-col{
        max-width: 330px;
      }
    }
    &__title{
      font-size: 24px;
      line-height: 27px;
    }
    &__desc{
      margin-top: 8px;
      font-size: 18px;
      line-height: 24px;
    }
    &__square{
      font-size: 18px;
      line-height: 22px;
    }
    &__bottom{
      margin-top: 30px;
    }
    &__grid{
      margin-bottom: 10px;
      grid-template-columns: repeat(6,calc(100% / 6 - 50px / 6));
      gap: 10px;
    }
  }
}


@media screen and (max-width: 768px){
  .landing-group{
    &__top{
      display: block;
      &-col2{
        margin-top: 14px;
        padding-top: 14px;
        display: flex;
        border-top: 1px solid #C9C9C9;
      }
    }
    &__desc2{
      margin-right: 8px;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 5px));
    }
    &__item{
      grid-column: span 1;
      &._big-col-mob{
        grid-column: span 2;
      }
      &._big-row{
        padding-bottom: 150%;
        grid-row: span 2;
      }
      &._big-col2{
        grid-column: span 2;
      }
    }
  }
}
